.title {
  font-family: "SFProDisplay-Regular", sans-serif;
  font-weight: 400;
}

.top__title {
  font-family: "SFProDisplay-Semibold", sans-serif;
  font-weight: 600;
}

body {
  font-family: "Ubuntu", sans-serif;
  font-weight: 300;
}

.product__slider-text, .header__inner {
  font-family: "Ubuntu", sans-serif;
  font-weight: 500;
}

.header__phone {
  font-family: "Ubuntu", sans-serif;
  font-weight: 700;
}

