  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

@font-face {
  font-family: "SFProDisplay-Light", sans-serif;
  src: url("../fonts/SFProDisplay-Light.woff2") format("woff2"), url("../fonts/SFProDisplay-Light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "SFProDisplay-Regular", sans-serif;
  src: url("../fonts/SFProDisplay-Light.woff2") format("woff2"), url("../fonts/SFProDisplay-Light.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "SFProDisplay-Medium", sans-serif;
  src: url("../fonts/SFProDisplay-Light.woff2") format("woff2"), url("../fonts/SFProDisplay-Light.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "SFProDisplay-Semibold", sans-serif;
  src: url("../fonts/SFProDisplay-Light.woff2") format("woff2"), url("../fonts/SFProDisplay-Light.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "SFProDisplay-Bold", sans-serif;
  src: url("../fonts/SFProDisplay-Light.woff2") format("woff2"), url("../fonts/SFProDisplay-Light.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, *::before, *::after {
  box-sizing: inherit;
}

a {
  text-decoration: none;
  color: inherit;
}
a:hover {
  cursor: pointer;
}

ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-size: inherit;
}

body {
  min-width: 320px;
  overflow-x: hidden;
  position: relative;
  color: #fff;
  font-size: 17px;
  line-height: 25px;
}

.container {
  max-width: 1270px;
  padding: 0 15px;
  margin: 0 auto;
}

.page-section {
  background-image: url(../images/page-bg.jpg);
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;
  position: relative;
}
.page-section::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(107.54deg, rgba(23, 26, 28, 0.72) 0%, #5E5E57 100%);
  opacity: 0.15;
}
.page-section__wrapper {
  min-height: 100vh;
  position: relative;
  z-index: 1;
  padding: 74px 0 50px;
}

.title {
  font-size: 44px;
  line-height: 53px;
  padding-left: 25px;
  margin-bottom: 55px;
  padding-top: 200px;
}

.scroll-btn {
  padding: 13px 0 30px;
  text-align: center;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
}

.header {
  position: fixed;
  left: 0;
  top: 30px;
  right: 0;
  z-index: 5;
}
.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 30px;
  font-size: 15px;
  line-height: 17px;
}
.header__btn {
  color: #fff;
  background-color: transparent;
  padding: 6px 28px;
  border: 1px solid #FFFFFF;
  border-radius: 3px;
  margin-right: 8px;
  text-transform: uppercase;
  cursor: pointer;
  transition: all 0.3s;
}
.header__btn:hover {
  color: #000;
  background: #fff;
}
.header__phone {
  font-size: 16px;
  line-height: 18px;
}
.header .logo {
  opacity: 1;
  transition: all 0.3s;
}
.header .logo:hover {
  opacity: 50%;
}

.menu__list-item {
  display: inline-block;
  transition: all 0.3s;
}
.menu__list-item + .menu__list-item {
  padding-left: 20px;
}
.menu__list-item:hover {
  opacity: 50%;
}

.top {
  background-image: url("../images/top-bg.jpg");
}
.top::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(45.97% 54.43% at 50% 45.57%, rgba(23, 26, 28, 0) 50.28%, rgba(23, 26, 28, 0.00225974) 50.29%, rgba(4, 5, 5, 0.820343) 58.72%, rgba(5, 5, 6, 0.792703) 80.08%, #000000 87.89%);
  opacity: 0.4;
}
.top__wrapper {
  display: flex;
  align-items: flex-end;
}
.top__inner {
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
}
.top__title {
  font-size: 96px;
  line-height: 115px;
  margin-bottom: 25px;
}
.top__text {
  margin: 25px 0 35px;
  max-width: 325px;
}
.top__social-item {
  display: inline-block;
  margin: 0 25px;
}
.top__social-link {
  opacity: 50%;
  transition: all 0.3s;
}
.top__social-link:hover {
  opacity: 100%;
}

.product__inner {
  display: flex;
  justify-content: space-between;
  padding-left: 50px;
}
.product__text {
  max-width: 440px;
}
.product__text p {
  margin-bottom: 30px;
}
.product__slider-text {
  width: 285px;
  position: absolute;
  text-align: right;
  font-size: 24px;
  line-height: 28px;
  bottom: 30px;
  left: -250px;
  z-index: 5;
}
.product__slider-box {
  position: relative;
  padding-top: 100px;
}
.product__slider {
  max-width: 560px;
}